import { Component, Input, Output, EventEmitter, OnInit, OnChanges, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { NzModalService } from 'ng-zorro-antd/modal';
import { PriorAuthItem, PriorAuthMilestone, PriorAuthItemCodes, PriorAuthAction, PriorAuthComment } from '../../../models/prior-auth.model';
import { PriorAuthService } from '../../../services/prior-auth.service';
import { MessagingService } from '../../../services/messaging.service';
import { CernerActionService } from '../../../services/cerner-action.service';
import { FeedbackFormComponent, FeedbackData } from '../../feedback-form/feedback-form.component';

interface MilestoneDetail {
  name: string;
  status: string;
  date?: Date;
  notes?: string;
  actor?: string;
}

interface CodeGroup {
  type: string;
  codes: PriorAuthItemCodes[];
}

/**
 * Interface for timeline items that can be either milestones or actions
 */
interface TimelineItem {
  id: number;
  type: 'milestone' | 'action';
  name: string;
  description?: string;
  status: string;
  dateTime: Date;
  milestoneId?: number; // For actions that belong to a milestone
  meaning?: string;
  milestoneData?: any[];
  required?: boolean; // Added for milestones
  actionBy?: string; // The user who performed the action (for actions only)
}

@Component({
  selector: 'app-milestone-details',
  templateUrl: './milestone-details.component.html',
  styleUrls: ['./milestone-details.component.scss']
})
export class MilestoneDetailsComponent implements OnInit, OnChanges {
  @Input() worklistItem: PriorAuthItem | null = null;
  @Input() visible = false;
  @Output() visibleChange = new EventEmitter<boolean>();
  /**
   * Emits when the worklist item is refreshed so the parent can update the table row.
   */
  @Output() itemRefreshed = new EventEmitter<PriorAuthItem>();
  
  milestoneDetails: MilestoneDetail[] = [];
  timelineItems: TimelineItem[] = [];
  isLoading = false;
  drawerWidth = 1000;
  isRefreshing = false;
  
  // Track which milestones have expanded action lists
  expandedMilestones = new Set<number>();
  
  // Number of actions to show initially
  readonly INITIAL_ACTIONS_COUNT = 4;
  
  // Track copying state
  isCopying = false;
  
  /**
   * List of comments for the current worklist item
   */
  comments: PriorAuthComment[] = [];
  /**
   * Loading state for comments
   */
  isCommentsLoading = false;
  /**
   * New comment text for the add comment form
   */
  newCommentText = '';
  
  constructor(
    private worklistService: PriorAuthService,
    private modalService: NzModalService,
    private messagingService: MessagingService,
    private cernerActionService: CernerActionService,
    private cdr: ChangeDetectorRef // Inject ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    if (this.worklistItem) {
      this.loadMilestoneDetails();
      this.buildTimeline();
      if (this.visible) {
        this.loadComments();
      }
    }
  }
  
  ngOnChanges(changes: SimpleChanges): void {
    if (changes['worklistItem'] && this.worklistItem) {
      this.loadMilestoneDetails();
      this.buildTimeline();
      if (this.visible) {
        this.loadComments();
      }
    }
    if (changes['visible'] && this.visible && this.worklistItem) {
      this.loadComments();
    }
  }

  /**
   * Build a grouped timeline: for each milestone (in sequence), add the milestone, then its actions (sorted by actionId). Standalone actions at the end.
   */
  buildTimeline(): void {
    if (!this.worklistItem) return;

    this.timelineItems = [];

    const meaningOrder = ['EDOS', 'MEDNEC', 'GLIDIAN', 'SCHEDULE'];
    const milestones = (this.worklistItem.milestones || []).slice().sort((a, b) => {
      const aIndex = meaningOrder.indexOf(a.meaning || '');
      const bIndex = meaningOrder.indexOf(b.meaning || '');
      return aIndex - bIndex;
    });
    const actions = (this.worklistItem.actions || []).slice();

    // For each milestone, add milestone then its actions
    milestones.forEach(milestone => {
      this.timelineItems.push({
        id: milestone.milestoneId,
        type: 'milestone',
        name: milestone.name,
        description: `Milestone: ${milestone.name}`,
        status: milestone.status,
        dateTime: milestone.statusDateTime || new Date(),
        meaning: milestone.meaning,
        milestoneData: milestone.milestoneData,
        required: milestone.requiredInd
      });
      // Add actions for this milestone, sorted by actionId
      actions
        .filter(action => action.milestoneId === milestone.milestoneId)
        .sort((a, b) => a.actionId - b.actionId)
        .forEach(action => {
          this.timelineItems.push({
            id: action.actionId,
            type: 'action',
            name: action.name,
            description: action.description,
            status: action.status,
            dateTime: action.actionDtTm,
            milestoneId: action.milestoneId,
            actionBy: action.actionBy
          });
        });
    });

    // Add standalone actions (milestoneId === 0) at the end, sorted by actionId
    actions
      .filter(action => !action.milestoneId)
      .sort((a, b) => a.actionId - b.actionId)
      .forEach(action => {
        this.timelineItems.push({
          id: action.actionId,
          type: 'action',
          name: action.name,
          description: action.description,
          status: action.status,
          dateTime: action.actionDtTm,
          milestoneId: action.milestoneId
        });
      });
  }

  /**
   * Get timeline item color based on type and status
   */
  getTimelineItemColor(item: TimelineItem): 'red' | 'green' | 'blue' | 'grey' | 'gray' {
    if (item.type === 'milestone') {
      switch (item.status?.toLowerCase()) {
        case 'complete':
          return 'green';
        case 'in progress':
        case 'open':
          return 'blue';
        case 'pending':
          return 'red';
        default:
          return 'gray';
      }
    } else {
      // Action colors
      switch (item.status?.toLowerCase()) {
        case 'completed':
          return 'green';
        case 'in progress':
          return 'blue';
        case 'pending':
          return 'red';
        default:
          return 'gray';
      }
    }
  }

  /**
   * Get milestone name for an action if it belongs to a milestone
   */
  getMilestoneNameForAction(milestoneId: number): string | null {
    if (!milestoneId || milestoneId === 0) return null;
    
    const milestone = this.worklistItem?.milestones?.find(m => m.milestoneId === milestoneId);
    return milestone?.name || null;
  }

  /**
   * Check if an action is standalone (not associated with a milestone)
   */
  isStandaloneAction(action: PriorAuthAction): boolean {
    return !action.milestoneId || action.milestoneId === 0;
  }

  onDrawerWidthChange(width: number): void {
    this.drawerWidth = width;
  }

  /**
   * Get priority color for tags
   */
  getPriorityColor(priority: string): string {
    switch (priority?.toLowerCase()) {
      case 'high':
        return 'red';
      case 'normal':
        return 'blue';
      case 'low':
        return 'green';
      default:
        return 'default';
    }
  }

  /**
   * Get codes grouped by type
   */
  getCodesByType(): CodeGroup[] {
    if (!this.worklistItem?.codeList || this.worklistItem.codeList.length === 0) {
      return [];
    }

    const codeGroups: { [key: string]: PriorAuthItemCodes[] } = {};
    
    this.worklistItem.codeList.forEach(code => {
      if (!codeGroups[code.type]) {
        codeGroups[code.type] = [];
      }
      codeGroups[code.type].push(code);
    });

    return Object.keys(codeGroups).map(type => ({
      type: type,
      codes: codeGroups[type]
    }));
  }

  loadMilestoneDetails(): void {
    if (!this.worklistItem) return;
    
    this.isLoading = true;
    
    // In a real app, you would call a service to fetch detailed milestone data
    // For now, we'll transform existing milestone data for demonstration purposes
    this.worklistService.getMilestoneDetails(this.worklistItem.compass_worklist_id)
      .subscribe(details => {
        this.milestoneDetails = details;
        this.isLoading = false;
      });
  }
  
  updateMilestoneStatus(milestoneName: string, newStatus: string): void {
    if (!this.worklistItem) return;
    this.worklistService.updatePriorAuthItemMilestone(
      this.worklistItem.compass_worklist_id,
      milestoneName,
      newStatus
    );
    // Refresh the details
    this.loadMilestoneDetails();
  }
  
  addMilestoneNote(milestoneName: string, note: string): void {
    if (!this.worklistItem) return;
    
    this.worklistService.addMilestoneNote(
      this.worklistItem.compass_worklist_id,
      milestoneName,
      note
    );
    
    // Refresh the details
    this.loadMilestoneDetails();
  }
  
  close(): void {
    this.visible = false;
    this.visibleChange.emit(false);
  }

  /**
   * Open feedback modal for the current item
   */
  openFeedbackModal(): void {
    if (!this.worklistItem) return;

    const modalRef = this.modalService.create({
      nzTitle: 'Submit Feedback',
      nzContent: FeedbackFormComponent,
      nzWidth: 500,
      nzData: {
        itemReference: this.worklistItem.itemDescription || 'N/A',
        itemType: 'Prior Authorization',
        itemMeaning: "WORKLISTITEM",
        itemId: this.worklistItem.compass_worklist_id,
        item: this.worklistItem
      },
      nzFooter: null,
      nzClosable: true,
      nzMaskClosable: false
    });

    // Handle feedback submission
    modalRef.afterClose.subscribe((result: FeedbackData | undefined) => {
      if (result) {
        // Feedback was successfully submitted for the current worklist item
      }
    });
  }

  /**
   * Refresh the current worklist item by ID
   */
  refreshCurrentItem(): void {
    if (!this.worklistItem) return;
    this.isRefreshing = true;
    // Initiate refresh of the current worklist item data
    this.worklistService.getPriorAuthItemById(this.worklistItem.compass_worklist_id).subscribe({
      next: (updatedItem) => {
        if (updatedItem) {
          // Update the current item with the latest data from the server
          this.worklistItem = updatedItem;
          this.itemRefreshed.emit(updatedItem); // Notify parent
          this.buildTimeline(); // Rebuild timeline with updated data
        }
      },
      error: (err) => {
        // Handle error during item refresh - could show user notification
      },
      complete: () => {
        // Item refresh operation completed
        this.isRefreshing = false;
      }
    });
  }

  /**
   * Modify the order using CernerActionService
   * Builds the required object and calls the MODIFY_ORDER action
   */
  async modifyOrder(): Promise<void> {
    if (!this.worklistItem) {
      this.messagingService.error('No worklist item available for order modification');
      return;
    }

    if (!this.worklistItem.orderId || this.worklistItem.orderId <= 0) {
      this.messagingService.error('No valid order ID available for modification');
      return;
    }

    try {
      // Build the data object for the CernerAction service
      const actionData = {
        orderId: this.worklistItem.orderId,
        personId: this.worklistItem.personId,
        encntrId: this.worklistItem.encntrId || 0,
        // Optional parameters with defaults
        enablePowerPlans: false,
        defaultDisplay: 32, // Default to order profile
        silentSign: false   // Default to normal operation
      };

      console.log('[MilestoneDetailsComponent] Calling CernerAction MODIFY_ORDER with data:', actionData);
      
      // Call the CernerAction service
      await this.cernerActionService.CernerAction('MODIFY_ORDER', actionData);
      
      this.messagingService.success('Opening order modification window...');
      
    } catch (error) {
      console.error('[MilestoneDetailsComponent] Error modifying order:', error);
      this.messagingService.error('Failed to open order modification window');
    }
  }

  /**
   * Cancel the order using CernerActionService
   * Builds the required object and calls the CANCEL_ORDER action
   */
  async cancelOrder(): Promise<void> {
    if (!this.worklistItem) {
      this.messagingService.error('No worklist item available for order cancellation');
      return;
    }

    if (!this.worklistItem.orderId || this.worklistItem.orderId <= 0) {
      this.messagingService.error('No valid order ID available for cancellation');
      return;
    }

    try {
      // Build the data object for the CernerAction service
      const actionData = {
        orderId: this.worklistItem.orderId,
        personId: this.worklistItem.personId,
        encntrId: this.worklistItem.encntrId || 0,
        // Optional parameters with defaults
        enablePowerPlans: false,
        defaultDisplay: 32, // Default to order profile
        silentSign: false   // Default to normal operation
      };

      console.log('[MilestoneDetailsComponent] Calling CernerAction CANCEL_ORDER with data:', actionData);
      
      // Call the CernerAction service
      await this.cernerActionService.CernerAction('CANCEL_ORDER', actionData);
      
      this.messagingService.success('Opening order cancellation window...');
      
    } catch (error) {
      console.error('[MilestoneDetailsComponent] Error canceling order:', error);
      this.messagingService.error('Failed to open order cancellation window');
    }
  }

  /**
   * Cancel and reorder using CernerActionService
   * Builds the required object and calls the CANCEL_REORDER action
   */
  async cancelReorder(): Promise<void> {
    if (!this.worklistItem) {
      this.messagingService.error('No worklist item available for cancel and reorder');
      return;
    }

    if (!this.worklistItem.orderId || this.worklistItem.orderId <= 0) {
      this.messagingService.error('No valid order ID available for cancel and reorder');
      return;
    }

    try {
      // Build the data object for the CernerAction service
      const actionData = {
        orderId: this.worklistItem.orderId,
        personId: this.worklistItem.personId,
        encntrId: this.worklistItem.encntrId || 0,
        // Optional parameters with defaults
        enablePowerPlans: false,
        defaultDisplay: 32, // Default to order profile
        silentSign: false   // Default to normal operation
      };

      console.log('[MilestoneDetailsComponent] Calling CernerAction CANCEL_REORDER with data:', actionData);
      
      // Call the CernerAction service
      await this.cernerActionService.CernerAction('CANCEL_REORDER', actionData);
      
      this.messagingService.success('Opening cancel and reorder window...');
      
    } catch (error) {
      console.error('[MilestoneDetailsComponent] Error canceling and reordering:', error);
      this.messagingService.error('Failed to open cancel and reorder window');
    }
  }

  /**
   * Reprocess COMPASS for the current worklist item by calling the backend service.
   * Sends requestType 'reprocessCompass' and the compass_worklist_id as requestId.
   */
  reprocessCompass(): void {
    if (!this.worklistItem) return;
    const worklistId = this.worklistItem.compass_worklist_id;
    this.isLoading = true;
    this.worklistService.reprocessCompass(worklistId).subscribe({
      next: (result) => {
        this.messagingService.success('COMPASS reprocessing triggered successfully.');
        // Automatically refresh the worklist item after reprocessing
        this.refreshCurrentItem();
      },
      error: (err) => {
        this.messagingService.error('Failed to reprocess COMPASS.');
      },
      complete: () => {
        this.isLoading = false;
      }
    });
  }

  /**
   * Get milestone timeline items
   */
  get milestoneItems(): TimelineItem[] {
    return this.timelineItems.filter(item => item.type === 'milestone');
  }

  /**
   * Get actions for a specific milestone
   */
  getActionsForMilestone(milestoneId: number): TimelineItem[] {
    return this.timelineItems.filter(item => 
      item.type === 'action' && item.milestoneId === milestoneId
    );
  }

  /**
   * Get standalone actions (not associated with any milestone)
   */
  get standaloneActions(): TimelineItem[] {
    return this.timelineItems.filter(item => 
      item.type === 'action' && (!item.milestoneId || item.milestoneId === 0)
    );
  }

  /**
   * Get actions for a specific milestone with visibility control
   */
  getVisibleActionsForMilestone(milestoneId: number): TimelineItem[] {
    const allActions = this.getActionsForMilestone(milestoneId);
    const isExpanded = this.expandedMilestones.has(milestoneId);
    
    if (isExpanded) {
      return allActions;
    } else {
      return allActions.slice(0, this.INITIAL_ACTIONS_COUNT);
    }
  }

  /**
   * Check if a milestone has more actions than the initial display limit
   */
  hasMoreActions(milestoneId: number): boolean {
    const allActions = this.getActionsForMilestone(milestoneId);
    return allActions.length > this.INITIAL_ACTIONS_COUNT;
  }

  /**
   * Check if a milestone's actions are expanded
   */
  isMilestoneExpanded(milestoneId: number): boolean {
    return this.expandedMilestones.has(milestoneId);
  }

  /**
   * Toggle the expanded state of a milestone's actions
   */
  toggleMilestoneActions(milestoneId: number): void {
    if (this.expandedMilestones.has(milestoneId)) {
      this.expandedMilestones.delete(milestoneId);
    } else {
      this.expandedMilestones.add(milestoneId);
    }
  }

  /**
   * Get the count of hidden actions for a milestone
   */
  getHiddenActionsCount(milestoneId: number): number {
    const allActions = this.getActionsForMilestone(milestoneId);
    const visibleCount = this.isMilestoneExpanded(milestoneId) 
      ? allActions.length 
      : Math.min(allActions.length, this.INITIAL_ACTIONS_COUNT);
    
    return Math.max(0, allActions.length - visibleCount);
  }

  /**
   * Copy the entire worklist item data to clipboard as JSON
   */
  async copyItemDataToClipboard(): Promise<void> {
    console.log('[MilestoneDetailsComponent] copyItemDataToClipboard called');
    console.log('[MilestoneDetailsComponent] worklistItem:', this.worklistItem);
    
    if (!this.worklistItem) {
      console.warn('[MilestoneDetailsComponent] No worklist item available for copying');
      return;
    }
    
    this.isCopying = true;
    console.log('[MilestoneDetailsComponent] Copying started, isCopying:', this.isCopying);
    
    try {
      // Create a clean copy of the worklist item data
      const itemData = {
        compass_worklist_id: this.worklistItem.compass_worklist_id,
        personId: this.worklistItem.personId,
        encntrId: this.worklistItem.encntrId,
        orderId: this.worklistItem.orderId,
        workflow_id: this.worklistItem.workflow_id,
        itemDescription: this.worklistItem.itemDescription,
        workflowType: this.worklistItem.workflowType,
        priority: this.worklistItem.priority,
        orderingLocation: this.worklistItem.orderingLocation,
        orderingProvider: this.worklistItem.orderingProvider,
        schedulingLocation: this.worklistItem.schedulingLocation,
        origOrderDtTm: this.worklistItem.origOrderDtTm,
        edosDate: this.worklistItem.edosDate,
        workflowUpdateDateTime: this.worklistItem.workflowUpdateDateTime,
        workflowUpdateCount: this.worklistItem.workflowUpdateCount,
        dataRefreshDtTm: this.worklistItem.dataRefreshDtTm,
        patient: this.worklistItem.patient,
        workflowStatus: this.worklistItem.workflowStatus,
        subStatus: this.worklistItem.subStatus,
        schedule: this.worklistItem.schedule,
        codeList: this.worklistItem.codeList,
        payors: this.worklistItem.payors,
        milestones: this.worklistItem.milestones,
        actions: this.worklistItem.actions,
        // Add any additional fields that might be useful for troubleshooting
        _exportedAt: new Date().toISOString(),
        _exportedBy: 'Milestone Details Component'
      };
      
      console.log('[MilestoneDetailsComponent] Item data prepared:', itemData);
      
      // Convert to formatted JSON string
      const jsonString = JSON.stringify(itemData, null, 2);
      console.log('[MilestoneDetailsComponent] JSON string created, length:', jsonString.length);
      
      // Copy to clipboard
      await navigator.clipboard.writeText(jsonString);
      console.log('[MilestoneDetailsComponent] Data copied to clipboard successfully');
      
      // Show success message
      this.messagingService.success('Item data copied to clipboard successfully');
      
    } catch (error) {
      console.error('[MilestoneDetailsComponent] Failed to copy item data to clipboard:', error);
      this.messagingService.error('Failed to copy item data to clipboard');
    } finally {
      this.isCopying = false;
      console.log('[MilestoneDetailsComponent] Copying finished, isCopying:', this.isCopying);
    }
  }

  /**
   * Loads comments for the current worklist item using PriorAuthService
   */
  loadComments(): void {
    if (!this.worklistItem) return;
    this.isCommentsLoading = true;
    this.worklistService.getWorklistComments(this.worklistItem.compass_worklist_id)
      .subscribe({
        next: (comments) => {
          this.comments = comments;
          this.isCommentsLoading = false;
          this.cdr.detectChanges(); // Force UI update
        },
        error: () => {
          this.comments = [];
          this.isCommentsLoading = false;
          this.cdr.detectChanges(); // Force UI update
        }
      });
  }

  /**
   * Adds a new comment to the current worklist item and reloads the comment list
   */
  addComment(): void {
    if (!this.worklistItem || !this.newCommentText.trim()) return;
    this.isCommentsLoading = true;
    this.worklistService.addWorklistComment(this.worklistItem.compass_worklist_id, this.newCommentText.trim())
      .subscribe({
        next: (comment) => {
          this.newCommentText = '';
          this.loadComments();
        },
        error: () => {
          this.isCommentsLoading = false;
        }
      });
  }
}