import { Component, OnInit, OnDestroy, ViewChild, Input, ChangeDetectionStrategy, signal, computed } from '@angular/core';
import { PriorAuthItem, PriorAuthMilestone } from '../../../models/prior-auth.model';
import { ColumnItem } from '../../../models/column-item.model';
import { PriorAuthService } from '../../../services/prior-auth.service';
import { MilestoneDetailsComponent } from '../milestone-details/milestone-details.component';
import { ColumnConfigService } from '../../../services/column-config.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

/**
 * WorklistTableComponent - Prior Authorization Worklist Data Table
 * 
 * This component displays the main data table for prior authorization worklist items,
 * providing a comprehensive view of all pending authorization requests with interactive
 * features for item management, filtering, and detailed milestone tracking.
 * 
 * COMPONENT ARCHITECTURE:
 * - Uses Angular Signals for reactive state management
 * - Implements OnPush change detection strategy for optimal performance
 * - Integrates with NG-Zorro Ant Design table component for rich UI features
 * - Supports expandable rows for additional item details
 * - Features sliding drawer for milestone management
 * 
 * SIGNAL-BASED STATE MANAGEMENT:
 * - _expandedIds: Tracks which table rows are expanded
 * - _loadingItems: Manages loading states for individual items
 * - _drawerVisible: Controls milestone details drawer visibility
 * - _selectedItem: Holds currently selected item for drawer display
 * - Computed signals provide derived state for template consumption
 * 
 * KEY FEATURES:
 * - Real-time data updates via signal reactivity
 * - Interactive milestone status updates
 * - Expandable row details with patient and order information
 * - Sliding drawer for comprehensive milestone management
 * - Dynamic column configuration and sorting
 * - Integrated feedback system for user input
 * - Responsive design for various screen sizes
 * 
 * DATA FLOW:
 * 1. Receives filtered worklist items from PriorAuthService
 * 2. Displays items in sortable, filterable table format
 * 3. Handles user interactions (expand, select, status updates)
 * 4. Triggers milestone updates and refreshes data as needed
 * 5. Provides feedback collection for continuous improvement
 * 
 * PERFORMANCE OPTIMIZATIONS:
 * - OnPush change detection reduces unnecessary re-renders
 * - Signals provide fine-grained reactivity without full component updates
 * - Lazy loading of milestone details via drawer interaction
 * - Optimized row expansion tracking using Set data structure
 * 
 * INTEGRATION POINTS:
 * - PriorAuthService: Primary data source and state management
 * - ColumnConfigService: Dynamic column configuration
 * - MessagingService: User notifications and feedback
 * - FeedbackService: User feedback collection and submission
 * - Various cell components: Specialized rendering for different data types
 */
@Component({
  selector: 'app-worklist-table',
  templateUrl: './worklist-table.component.html',
  styleUrls: ['./worklist-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WorklistTableComponent implements OnInit, OnDestroy {
  @ViewChild(MilestoneDetailsComponent) milestoneDetails!: MilestoneDetailsComponent;
  
  // === SIGNALS IMPLEMENTATION ===
  
  /**
   * Signal tracking which rows are expanded in the table
   * Provides better performance than traditional Set operations
   */
  private _expandedIds = signal<Set<number>>(new Set());
  
  /**
   * Signal tracking which items are currently loading
   * Used to show loading indicators on individual rows
   */
  private _loadingItems = signal<Set<number>>(new Set());
  
  /**
   * Signal tracking drawer visibility state
   * Controls whether the milestone details drawer is shown
   */
  private _drawerVisible = signal<boolean>(false);
  
  /**
   * Signal tracking the currently selected worklist item
   * Used for displaying details in the drawer
   */
  private _selectedItem = signal<PriorAuthItem | null>(null);
  
  /**
   * Persistent filter state for each filterable column.
   * Keyed by column meaning, value is array of selected filter values.
   * This is used to re-apply filters after data refresh.
   */
  filterSelections: { [columnMeaning: string]: string[] } = {};
  
  // === READ-ONLY SIGNALS FOR EXTERNAL ACCESS ===
  
  /**
   * Read-only access to expanded row IDs
   * Components and templates can use this to check expansion state
   */
  readonly expandedIds = this._expandedIds.asReadonly();
  
  /**
   * Read-only access to loading items set
   * Used to show loading spinners on specific rows
   */
  readonly loadingItems = this._loadingItems.asReadonly();
  
  /**
   * Read-only access to drawer visibility state
   * Controls drawer open/close state in template
   */
  readonly drawerVisible = this._drawerVisible.asReadonly();
  
  /**
   * Read-only access to selected item
   * Used to display item details in drawer
   */
  readonly selectedItem = this._selectedItem.asReadonly();
  
  // === COMPUTED SIGNALS FOR DERIVED STATE ===
  
  /**
   * Computed signal that returns the current column configuration
   * Automatically updates when column configuration changes
   */
  readonly listOfColumns = computed(() => 
    this.columnConfig.getColumns('prior-auth')
  );
  
  /**
   * Computed helper functions for template use
   * These provide functional access to check states without repeated calculations
   */
  readonly isItemExpanded = computed(() => 
    (id: number) => this._expandedIds().has(id)
  );
  
  readonly isItemLoading = computed(() => 
    (id: number) => this._loadingItems().has(id) || this.worklistService.isItemLoadingDetails(id)
  );
  
  // === LIFECYCLE AND CLEANUP ===
  
  /**
   * Subject for handling component destruction and subscription cleanup
   */
  private destroy$ = new Subject<void>();
  
  /**
   * The worklist items to display, provided by the parent component.
   * This remains as an Input for compatibility with parent components
   */
  @Input() worklistItems: PriorAuthItem[] = [];
  
  /**
   * Loading state for the entire table, provided by the parent component.
   * Used to show loading spinner when data is being fetched
   */
  @Input() loading = false;
  
  constructor(
    private worklistService: PriorAuthService,
    private columnConfig: ColumnConfigService
  ) {}

  ngOnInit(): void {
    // Load saved column widths
    this.columnConfig.loadColumnWidths();

    // Restore filter selections if present (e.g., after a data refresh)
    setTimeout(() => {
      this.restoreTableFilters();
    }, 0);

    // Subscribe to automatic item refresh events from the service
    // Note: The service already handles the refresh, so we just need to update the UI state
    this.worklistService.itemRefresh$
      .pipe(takeUntil(this.destroy$))
      .subscribe(itemId => {
        console.log(`[WorklistTableComponent] Received itemRefresh event for item ${itemId}`);
        // The service already refreshed the item, so we just need to update loading state
        // The signals will automatically update the UI when the service updates the worklist
      });
  }
  
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * Handle row expansion/collapse events for detailed item views
   * 
   * This method manages the expansion state of table rows, allowing users to
   * view additional details for specific worklist items. It uses signals to
   * ensure reactive updates throughout the component hierarchy.
   * 
   * SIGNAL UPDATE PROCESS:
   * 1. Creates a new Set from current expanded IDs to ensure immutability
   * 2. Adds or removes the target ID based on the checked parameter
   * 3. Updates the signal with the new Set, triggering reactive updates
   * 4. Template automatically re-renders affected rows based on signal changes
   * 
   * @param id - The unique identifier of the worklist item
   * @param checked - True to expand the row, false to collapse it
   * 
   * @example
   * ```typescript
   * // Expand row for item with ID 123
   * this.onExpandChange(123, true);
   * 
   * // Collapse row for item with ID 123
   * this.onExpandChange(123, false);
   * ```
   */
  onExpandChange(id: number, checked: boolean): void {
    const currentExpanded = new Set(this._expandedIds());
    if (checked) {
      currentExpanded.add(id);
    } else {
      currentExpanded.delete(id);
    }
    this._expandedIds.set(currentExpanded);
  }

  /**
   * Update the workflow status of a prior authorization item
   * 
   * This method delegates to the PriorAuthService to update the item's workflow
   * status both in local signals and potentially in the backend system. The
   * update triggers automatic re-rendering of affected UI elements.
   * 
   * @param item - The prior authorization item to update
   * @param status - The new workflow status to assign
   * 
   * @example
   * ```typescript
   * this.updateStatus(priorAuthItem, 'Approved');
   * this.updateStatus(priorAuthItem, 'Pending Review');
   * ```
   */
  updateStatus(item: PriorAuthItem, status: string): void {
    this.worklistService.updatePriorAuthItemStatus(item.compass_worklist_id, status);
  }

  /**
   * Update the priority level of a prior authorization item
   * 
   * This method delegates to the PriorAuthService to update the item's priority
   * classification, which affects how the item is displayed and sorted in the
   * worklist interface.
   * 
   * @param item - The prior authorization item to update
   * @param priority - The new priority level ('Low', 'Normal', 'High', 'Critical')
   * 
   * @example
   * ```typescript
   * this.updatePriority(priorAuthItem, 'High');
   * this.updatePriority(priorAuthItem, 'Normal');
   * ```
   */
  updatePriority(item: PriorAuthItem, priority: string): void {
    this.worklistService.updatePriorAuthItemPriority(item.compass_worklist_id, priority);
  }

  /**
   * Update the status of a specific milestone within a prior authorization item
   * 
   * This method handles milestone-level status updates, which are used to track
   * progress through the authorization workflow. Each milestone represents a
   * specific step in the authorization process.
   * 
   * @param item - The prior authorization item containing the milestone
   * @param milestoneName - The name of the milestone to update
   * @param status - The new status for the milestone ('Pending', 'In Progress', 'Complete')
   * 
   * @example
   * ```typescript
   * this.updateMilestone(priorAuthItem, 'Initial Review', 'Complete');
   * this.updateMilestone(priorAuthItem, 'Insurance Verification', 'In Progress');
   * ```
   */
  updateMilestone(item: PriorAuthItem, milestoneName: string, status: string): void {
    this.worklistService.updatePriorAuthItemMilestone(item.compass_worklist_id, milestoneName, status);
  }

  /**
   * Get the appropriate CSS class for milestone status display
   * 
   * This helper method maps milestone status values to corresponding CSS classes
   * for consistent visual representation throughout the interface. It ensures
   * standardized styling for different milestone states.
   * 
   * @param status - The milestone status to map to a CSS class
   * @returns The CSS class name corresponding to the status
   * 
   * @example
   * ```typescript
   * const cssClass = this.getMilestoneStatusClass('Complete'); // Returns 'milestone-complete'
   * const cssClass = this.getMilestoneStatusClass('Pending'); // Returns 'milestone-pending'
   * ```
   */
  getMilestoneStatusClass(status: string): string {
    switch (status) {
      case 'Complete':
        return 'milestone-complete';
      case 'In Progress':
        return 'milestone-in-progress';
      case 'Pending':
        return 'milestone-pending';
      default:
        return '';
    }
  }
  
  /**
   * Open the milestone details drawer for comprehensive item management
   * 
   * This method displays a sliding drawer containing detailed milestone information
   * and management tools for the selected prior authorization item. It uses signals
   * to manage drawer state and item selection reactively.
   * 
   * DRAWER FUNCTIONALITY:
   * - Displays complete milestone timeline with status history
   * - Provides milestone update capabilities
   * - Shows detailed item information and context
   * - Enables feedback collection for specific items
   * - Supports milestone note addition and management
   * 
   * @param item - The prior authorization item to display in the drawer
   * 
   * @example
   * ```typescript
   * // Open drawer for a specific item
   * this.openMilestoneDetails(selectedPriorAuthItem);
   * ```
   */
  openMilestoneDetails(item: PriorAuthItem): void {
    this._selectedItem.set(item);
    this._drawerVisible.set(true);
  }
  
  /**
   * Close the milestone details drawer and clear item selection
   * 
   * This method hides the milestone details drawer and clears the currently
   * selected item, returning the interface to its default state. It ensures
   * proper cleanup of drawer state using signal updates.
   * 
   * STATE CLEANUP:
   * - Sets drawer visibility to false
   * - Clears selected item reference
   * - Triggers reactive updates in dependent components
   * - Maintains clean component state for future interactions
   * 
   * @example
   * ```typescript
   * // Close drawer when user clicks close button
   * this.closeMilestoneDetails();
   * ```
   */
  closeMilestoneDetails(): void {
    this._drawerVisible.set(false);
    this._selectedItem.set(null);
  }
  
  /**
   * Handle drawer visibility changes initiated by child components
   * 
   * This method responds to drawer visibility events from child components,
   * ensuring consistent state management when the drawer is closed through
   * various user interactions (close button, overlay click, escape key, etc.).
   * 
   * EVENT HANDLING:
   * - Responds to child component visibility events
   * - Maintains state consistency across component hierarchy
   * - Provides centralized drawer state management
   * - Ensures proper cleanup when drawer is closed
   * 
   * @param visible - The new visibility state for the drawer
   * 
   * @example
   * ```typescript
   * // Called when child component closes drawer
   * this.onDrawerVisibilityChange(false);
   * ```
   */
  onDrawerVisibilityChange(visible: boolean): void {
    if (!visible) {
      this.closeMilestoneDetails();
    }
  }

  /**
   * Handle milestone details opening request from GlidianWorkflowCellComponent
   * 
   * This method serves as an event handler for milestone details requests
   * originating from the Glidian workflow cell component. It provides a
   * consistent interface for opening milestone details regardless of the
   * triggering component.
   * 
   * @param item - The prior authorization item for which to show milestone details
   * 
   * @example
   * ```typescript
   * // Called when user clicks milestone details in workflow cell
   * this.onOpenMilestoneDetails(priorAuthItem);
   * ```
   */
  onOpenMilestoneDetails(item: PriorAuthItem): void {
    this.openMilestoneDetails(item);
  }

  /**
   * Refresh a specific worklist item from the server
   * Uses signals to track loading state and update item data
   */
  refreshWorklistItem(item: PriorAuthItem): void {
    this.refreshWorklistItemById(item.compass_worklist_id);
  }

  /**
   * Refresh a specific worklist item by ID with loading state
   * This method can be called by the service for automatic refreshes
   */
  refreshWorklistItemById(itemId: number): void {
    // Begin refresh process for a specific worklist item
    
    // Add item to loading set using signals
    const currentLoading = new Set(this._loadingItems());
    currentLoading.add(itemId);
    this._loadingItems.set(currentLoading);
    
    this.worklistService.getPriorAuthItemById(itemId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (updatedItem: PriorAuthItem) => {
          if (updatedItem) {
            const idx = this.worklistItems.findIndex((i: PriorAuthItem) => i.compass_worklist_id === updatedItem.compass_worklist_id);
            if (idx !== -1) {
              this.worklistItems[idx] = updatedItem;
              this.worklistItems = [...this.worklistItems]; // trigger change detection
              // Item has been successfully updated with fresh data from server
              // Data refresh timestamp has been updated to reflect latest sync
            }
          }
        },
        error: (err) => {
          // Handle refresh error - item data remains unchanged
        },
        complete: () => {
          // Remove item from loading set using signals
          const newLoading = new Set(this._loadingItems());
          newLoading.delete(itemId);
          this._loadingItems.set(newLoading);
          // Refresh operation completed - loading indicator will be hidden
        }
      });
  }

  /**
   * Handler for when the milestone-details drawer refreshes an item.
   * Updates the main worklistItems array so the table row reflects the latest data.
   */
  onDrawerItemRefreshed(updatedItem: PriorAuthItem): void {
    const idx = this.worklistItems.findIndex(i => i.compass_worklist_id === updatedItem.compass_worklist_id);
    if (idx !== -1) {
      this.worklistItems[idx] = updatedItem;
      // If using OnPush or signals, update the array reference to trigger change detection
      this.worklistItems = [...this.worklistItems];
    }
  }

  /**
   * Type guard to check if an object is a PriorAuthItem
   */
  private isWorklistItem(obj: any): obj is PriorAuthItem {
    return obj && typeof obj.compass_worklist_id === 'number' && typeof obj.patientName === 'string';
  }

  getCellValue(item: PriorAuthItem, columnName: string): any {
    switch (columnName) {
      case 'Patient':
        return item.patient?.name;
      case 'Order Description':
        return item.itemDescription;
      case 'Ordering Location':
        return item.orderingLocation;
      case 'Ordering Provider':
        return item.orderingProvider;
      case 'Scheduling Location':
        return item.schedulingLocation;
      case 'Schedule Status':
        return item.schedule?.state || 'N/A';
      case 'Workflow Status':
        return item.workflowStatus.status;
      case 'Order Status':
        return item.subStatus.subStatus;
      case 'Priority':
        return item.priority;
      case 'Codes':
        return item.codeList?.map((code: any) => code.identifier).join(', ') || '';
      case 'Payor':
        return item.payors.map((p: any) => p.planName).join(', ');
      case 'EDOS Date':
        return item.edosDate;
      case 'Order Date':
        if (item.origOrderDtTm) {
          const date = new Date(item.origOrderDtTm);
          const day = date.getDate().toString().padStart(2, '0');
          const month = date.toLocaleString('en-US', { month: 'short' });
          const year = date.getFullYear();
          const hours = date.getHours().toString().padStart(2, '0');
          const minutes = date.getMinutes().toString().padStart(2, '0');
          return `${day}-${month}-${year}`;
        }
        return '';
      case 'EDOS':
        if (item.edosDate) {
          const date = new Date(item.edosDate);
          const day = date.getDate().toString().padStart(2, '0');
          const month = date.toLocaleString('en-US', { month: 'short' });
          const year = date.getFullYear();
          const hours = date.getHours().toString().padStart(2, '0');
          const minutes = date.getMinutes().toString().padStart(2, '0');
          return `${day}-${month}-${year}`;
        }
        return '';
      case 'Assigned User':
        // This will be handled by the user-assignment component
        return '';
      default:
        return '';
    }
  }

  /**
   * Handle column resize events
   */
  onColumnResize(event: { columnKey: string, width: number }): void {
    if (event.columnKey === 'workflow' || event.columnKey === 'actions') {
      // Handle special columns that are not in the column configuration
      localStorage.setItem(`compass_${event.columnKey}_width`, event.width.toString());
    } else {
      // Update column width in the configuration service
      this.columnConfig.updateColumnWidth('prior-auth', event.columnKey, event.width);
    }
  }

  /**
   * Get saved width for workflow and actions columns
   */
  getFixedColumnWidth(columnKey: string): number | undefined {
    const saved = localStorage.getItem(`compass_${columnKey}_width`);
    return saved ? parseInt(saved) : undefined;
  }

  /**
   * Reset column widths to default
   */
  resetColumnWidths(): void {
    this.columnConfig.resetColumnWidths('prior-auth');
    this.columnConfig.loadColumnWidths();
    // If you use a local columns array, update it here as well
    // this.columns = this.columnConfig.getColumns('prior-auth');
  }

  /**
   * Handler for NG-Zorro table filter changes.
   * Updates the persistent filterSelections state.
   * @param filters - Array of filter objects from NG-Zorro table
   */
  onTableFilterChange(filters: any): void {
    // Defensive: handle both array and event cases
    if (Array.isArray(filters)) {
      filters.forEach((f: any) => {
        if (f.key && Array.isArray(f.value)) {
          this.filterSelections[f.key] = f.value;
        }
      });
    } else if (filters && filters.detail && Array.isArray(filters.detail)) {
      filters.detail.forEach((f: any) => {
        if (f.key && Array.isArray(f.value)) {
          this.filterSelections[f.key] = f.value;
        }
      });
    } else {
      // Unexpected payload, log for debugging
      console.warn('Unexpected nzFilterChange payload:', filters);
    }
  }

  /**
   * Restore table filters after data refresh by setting byDefault on column filters.
   * This ensures the user's filter selections are re-applied.
   */
  restoreTableFilters(): void {
    const columns = this.columnConfig.getColumns('prior-auth');
    for (const col of columns) {
      if (col.filterable && col.listOfFilter) {
        const selected = this.filterSelections[col.meaning] || [];
        for (const opt of col.listOfFilter) {
          opt.byDefault = selected.includes(opt.value);
        }
      }
    }
    // Trigger update to columns so the table re-renders with correct filters
    this.columnConfig.triggerUpdate();
  }
}