import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ColumnConfigService } from '../../services/column-config.service';
import { ColumnItem } from '../../models/column-item.model';
import { PriorAuthService } from '../../services/prior-auth.service';
import { PriorAuthItem } from '../../models/prior-auth.model';

/**
 * PriorAuthComponent - Prior Authorization Worklist Management
 * 
 * This is the main container component for the Prior Authorization tab, orchestrating
 * the display and management of authorization requests that require clerk processing.
 * It coordinates between the filter bar and worklist table to provide a comprehensive
 * workflow management interface.
 * 
 * COMPONENT RESPONSIBILITIES:
 * - Serves as the primary container for prior authorization functionality
 * - Manages communication between filter bar and worklist table components
 * - Handles initial data loading and refresh operations
 * - Coordinates sidebar state management for layout optimization
 * - Provides centralized error handling and user feedback
 * 
 * ARCHITECTURE INTEGRATION:
 * - Consumes signals from PriorAuthService for reactive data updates
 * - Delegates column management to ColumnConfigService
 * - Coordinates with parent components for sidebar state management
 * - Uses child components for specialized functionality (filters, table)
 * 
 * DATA FLOW:
 * 1. Initializes worklist data from PriorAuthService
 * 2. Provides filter controls via PriorAuthFilterBarComponent
 * 3. Displays filtered results via WorklistTableComponent
 * 4. Handles user interactions and data refresh requests
 * 5. Manages loading states and error conditions
 * 
 * SIGNAL INTEGRATION:
 * - Uses computed signals from PriorAuthService for filtered data
 * - Reactive updates eliminate manual subscription management
 * - Automatic re-rendering when underlying data changes
 * - Efficient change detection with minimal performance overhead
 * 
 * USER WORKFLOW:
 * - Clerks access this tab to view pending authorization requests
 * - Filter controls help focus on relevant work items
 * - Table provides detailed item information and action capabilities
 * - Milestone tracking enables progress monitoring and updates
 * - Feedback system captures user input for continuous improvement
 * 
 * LAYOUT MANAGEMENT:
 * - Responds to sidebar collapse/expand state for optimal space usage
 * - Coordinates with parent layout for consistent user experience
 * - Emits sidebar toggle events for layout state management
 * - Maintains responsive design across different screen sizes
 */
@Component({
  selector: 'app-prior-auth',
  templateUrl: './prior-auth.component.html',
  styleUrls: ['./prior-auth.component.scss']
})
export class PriorAuthComponent {
  @Input() SideBarIsCollapsed!: boolean;
  @Output() sideBarToggle = new EventEmitter<void>();
  listOfColumns: ColumnItem[];

  /**
   * The current days filter value.
   */
  currentDaysFilter: number | 'all' = 'all';

  /**
   * The current global search term for filtering the worklist.
   */
  globalSearchTerm: string = '';

  /**
   * Handler for the global search event from the filter bar.
   * Updates the search term and triggers filtering.
   */
  onGlobalSearch(term: string): void {
    this.globalSearchTerm = term;
  }

  /**
   * Reactive computed signal for filtered worklist items
   * This automatically updates when the service data changes or the global search term changes
   */
  get filteredWorklistItems(): PriorAuthItem[] {
    const items = this.worklistService.filteredItems();
    const search = this.globalSearchTerm.trim().toLowerCase();
    if (!search) return items;
    return items.filter(item => {
      // Patient Name
      const patientName = item.patient?.name?.toLowerCase() || '';
      // FIN
      const fin = item.patient?.fin?.toLowerCase() || '';
      // MRN
      const mrn = item.patient?.mrn?.toLowerCase() || '';
      // Order Description
      const orderDesc = item.itemDescription?.toLowerCase() || '';
      // Glidian Record ID (not displayed in columns, but searchable)
      const glidianRecordId = item.glidianData?.recordId?.toLowerCase() || '';
      // Add more fields here as needed
      return (
        patientName.includes(search) ||
        fin.includes(search) ||
        mrn.includes(search) ||
        orderDesc.includes(search) ||
        glidianRecordId.includes(search)
      );
    });
  }

  constructor(private columnConfig: ColumnConfigService, private worklistService: PriorAuthService) {
    this.listOfColumns = this.columnConfig.getColumns('prior-auth');
  }

  ngOnInit(): void {
    // Component initialization - setup filters and load initial worklist data
    // Initialize the current filter value from the service
    this.currentDaysFilter = this.worklistService.getCurrentDaysFilter();
    
    // Load worklist data - the service will handle progressive loading
    this.loadWorklistData();
  }

  /**
   * Load worklist data from the service
   */
  loadWorklistData(): void {
    // Fetch worklist data from the service
    // The service will handle progressive loading and the component will react automatically
    this.worklistService.refreshWorklist().subscribe({
      next: () => {
        // Data loading completed - the reactive signals will handle updates
        console.log('[PriorAuthComponent] Worklist data loaded successfully');
      },
      error: (error) => {
        // Handle worklist loading errors - could show user notification
        console.error('[PriorAuthComponent] Error loading worklist data:', error);
      }
    });
  }

  /**
   * Handles the days filter change event
   */
  onDaysFilterChange(days: number | 'all'): void {
    // Handle user-initiated days filter change
    this.currentDaysFilter = days;
    
    // Update the service state - this will automatically update the filteredItems signal
    this.worklistService.updateDaysFilter(days);
  }

  onSideBarToggle() {
    this.sideBarToggle.emit();
  }

  /**
   * Handler for column widths reset event from the filter bar.
   */
  onColumnWidthsReset(): void {
    // The reset is handled in the filter bar component
    // This method exists for completeness if needed in the future
  }

  /**
   * Handler for refresh data event from the filter bar.
   * Reloads the worklist data from the service.
   */
  onRefreshData(): void {
    // Handle user-initiated data refresh request
    this.loadWorklistData();
  }
  
  /**
   * Getter for the loading state from the service
   * Used in template to show loading spinner
   */
  get isLoading(): boolean {
    return this.worklistService.loading();
  }
}